.portfolio {
  padding: 100px 0;
  position: relative;
}

.portfolio .background {
  position: absolute;
  top: 19%;
  left: 0;
  z-index: -1;
  max-width: 50%;
}
@media (max-width: 767px) {
  .portfolio .background {
    display: none;
  }
}
.portfolio .head {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 40px;
}
.portfolio .head h2 {
  width: 250px;
  margin-bottom: 0;
}
.portfolio .content {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 20px;
}

.portfolio .content img {
  width: 100%;
  border-radius: 5px;
  box-shadow: 2px 2px 20px -8px #1e1e1e;
}

.portfolio .see-more {
  text-decoration: none;
  font-weight: bold;
  text-align: center;
  display: block;
  margin-top: 25px;
}
