.blog .card {
  background-color: #ef5350;
  color: white;
  padding: 30px 20px;
  box-shadow: 2px 2px 12px 2px #cccccccf;
  border-radius: 5px;
}

.blog .card .date {
  font-size: 11px;
  letter-spacing: 1px;
  margin-bottom: 20px;
}

.blog .card .text {
  font-size: 14px;
  letter-spacing: 1px;
  line-height: 1.6;
  color: #eee;
}

.blog .card .arrow {
  background: white;
  display: block;
  margin-top: 29px;
  margin-left: calc(100% - 20px);
}


.blog {
  padding-bottom: 100px;
}
.blog .head {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 40px;
}
.head .arrow {
  margin-bottom: 15px;
}

.blog .content {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 20px;
}
