.landing {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.landing > img {
  position: absolute;
  top: 0;
  right: 0;
  max-width: 50%;
  z-index: -1;
}
.landing .container {
  display: flex;
  align-items: center;
}
.landing .text {
flex-basis: 50%;
}
.landing .text h1 {
  font-size: 42px;
  line-height: 1.3;
}
.landing .text p {
  color: #777;
  font-size: 14px;
  margin: 20px 0;
  line-height: 1.7;
}
.landing .text .buttons {
  display: flex;
  align-items: center;
}

.landing .text .buttons .get-in-touch{
  text-decoration: none;
  font-weight: bold;
  font-size: 12px;
}

.landing .container > img {
  max-width: 50%;
  transform: translateX(130px);
}

@media (max-width: 991px) {
  .landing .text h1 {
    font-size: 34px;
  }
  .landing > img {
    top: 20%;
    max-width: 50%;
  }
}

@media (max-width: 767px) {
  .landing .container{
    flex-direction: column;
    text-align: center;
  }
  .landing .text {
    margin: 300px 0 100px ;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .landing .container > img {
    transform: none;
    max-width: 70%;
  }
  .landing {
    max-height: 140vh;
  }
  .landing > img {
    display: none;
  }
}