:root {
  --variable: red;
}
html {
  scroll-behavior: smooth;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Almarai', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.container {
  margin-left: auto;
  margin-right: auto;
}
@media (max-width: 767px) {
  .container {
    width: 75%;
  }
}
@media (min-width: 768px) {
  .container {
    width: 530px;
  }
}
@media (min-width: 992px) {
  .container {
    width: 700px;
  }
}
@media (min-width: 1200px) {
  .container {
    width: 900px;
  }
}

.btn { 
  color: white;
  padding: 10px 20px;
  text-decoration: none;
  border-radius: 15px;
  margin-right: 20px;
  font-weight: bold;
  font-size: 12px;
}

.label h4 {
    margin-bottom: 10px;
    font-size: 11px;
    text-transform: uppercase;  
}
.label h2 {
  font-size: 30px;
  line-height: 1.4;
}

.arrow {
  width: 20px;
  height: 1.5px;
  position: relative;
  display: inline-block;
  margin-left: 10px;
  cursor: pointer;
}
.arrow::before,
.arrow::after {
  content: '';
  position: absolute;
  width: 8px;
  height: 1.5px;
  background-color: inherit;
  right: -2px;
}
.arrow::before {
  top: -3px;
  transform: rotate(45deg);
}
.arrow::after {
  top: 3px;
  transform: rotate(-45deg);

}
.leftArrow {
  transform: rotate(180deg);
  width: 8px;
  margin-right: 10px;
  background-color: #ccc;
}


