header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}

header .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 30px 0;
}
header .logo {
  position: absolute;
  left: 50px;
  top: 35px;
  font-weight: bold;
  font-size: 20px;
}
header .logo::after {
  content: '';
  position: absolute;
  width: 19px;
  height: 3px;
  background-color: #ff3946;
  bottom: 3px;
  left: 16px;
}
header .menu {
  font-size: 22px;
  display: none;
}

header .links a {
  text-decoration: none;
  font-size: 12px;
  font-weight: bold;
  margin-right: 20px;
}
@media (max-width: 767px) {
  header .logo {
    display: block;
    position: relative;
    padding: 20px;
    margin-bottom: -30px;
    top: 0;
    left: 0;
  }
  header .logo::after {
    bottom: 24px;
    left: 36px;
  }
  header .menu {
    display: block;
    transition: transform 0.5s;
  }
  header .menu.show {
    transform: rotate(90deg);
  }
  header .links {
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 125px;
    left: -100%;
    background-color: #f2fafd;
    width: 100%;
    transition: left 0.5s;
    height: calc(100vh - 125px);
  }
  header .links.show {
    left: 0;
  }
  header .links a {
    padding: 20px 20px 20px 40px;
    font-size: 15px;
    margin: 0;
  }
  header .links a:not(:last-child) {
    border-bottom: 1px solid #333;
  }
}
header .buttons {
  display: flex;
}

header .buttons .language {
  position: relative;
  font-weight: bold;
  font-size: 12px;
  margin-right: 30px;
  display: flex;
  align-items: center;
}
header .buttons .language::after {
  content: '';
  width: 0;
  height: 0;
  border: 3px solid;
  border-color: #314584 transparent transparent transparent;
  transform: translate(3px, 2px);
}

header .buttons .contact-us {
  color: rgb(255, 57, 70);
  padding: 10px 15px;
  border-radius: 3px;
  box-shadow: 1px 2px 6px 1px #44444424;
  text-decoration: none;
  display: flex;
  align-items: center;
  font-size: 12px;
  font-weight: bold;
}
header .buttons .contact-us .line {
  width: 20px;
  height: 1.5px;
  position: relative;
  display: inline-block;
  background-color: #ff3946;
  margin-left: 10px;
}
header .buttons .contact-us .line::before,
header .buttons .contact-us .line::after {
  content: '';
  position: absolute;
  width: 8px;
  height: 1.5px;
  background-color: inherit;
  right: -2px;
}
header .buttons .contact-us .line::before {
  top: -3px;
  transform: rotate(45deg);
}
header .buttons .contact-us .line::after {
  top: 3px;
  transform: rotate(-45deg);
}
