.about {
  position: relative;
}

@media (max-width: 767px) {
  .about > img {
    display: none;
  }
}

.about > img.right {
  right: 0;
}




.services .container {
  display: flex;
  justify-content: space-between;
  padding: 100px 0 200px;
  flex-wrap: wrap;
}

.services .text,
.services .serv {
  flex-basis: 30%;
  border-radius: 5px;
}
.services .text h1 {
  margin-bottom: 30px;
  font-size: 30px;
}
.services .serv {
  padding: 30px;
  box-shadow: 2px 2px 5px 0px #cccccccf;
}
.services .serv img {
  width: 40px;
  margin-bottom: 30px;
}
.services .serv h2 {
  font-size: 23px;
  margin-bottom: 20px;
}
.services .serv p {
  font-size: 12px;
  line-height: 1.6
}

@media (max-width: 991px) {
  .services .text {
    flex-basis: 100%;
    text-align: center;
    margin-bottom: 100px;
  }
  .services .serv {
    flex-basis: 48%;
  }
}
@media (max-width: 767px) {
  .services .container {
    margin-top: 100px;
    padding-bottom: 0;
    margin-bottom: 100px;
  }
  .services .serv {
    flex-basis: 100%;
    margin-bottom: 50px;
    text-align: center;
  }
}




.about-us .container {
  display: flex;
  justify-content: center;
  align-items: center;
}
.about-us img,
.about-us .text{
  flex-basis: 50%;
}
.about-us img {
  max-width: 50%;
  transform: translatex(-130px);
}
.about-us .text h2 {
  width: 240px;
}
.about-us .text p {
  font-size: 12px;
  line-height: 2;
  margin: 30px 0;
}


@media (max-width: 991px) {
  .about-us img {
    max-width: 67%;
    transform: translatex(-70px);
    max-height: 200px;
    align-self: center;

  }
}

@media (max-width: 767px) {
  .about-us .container {
    text-align: center;
    flex-direction: column;
  }
  .about-us .text {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 50px;
  }
  .about-us .text span {
    display: none;
  }
  .about-us img {
    order: 2;
    transform: none;
  }
}