input,
textarea {
  background-color: #f2fafd;
  padding: 10px 5px;
  border: none;
  color: #314584;
  border: 1px solid rgb(49 69 132 / 0.02);
  font-weight: bold;
  letter-spacing: 0.1px;
}
input::placeholder,
textarea::placeholder {
  color: #314584;
}
input:focus,
textarea:focus {
  outline: none;
}

.contact .container {
  margin-bottom: 50px;
}
.contact .box {
  display: flex;
  justify-content: space-between;
  margin-top: 30px;
}
.contact .box input {
  flex-basis: 48%;
}

.contact .message {
  display: block;
  width: 100%;
  margin: 25px 0 50px;
  height: 100px;
  resize: none;
}

.contact .btn {
  display: block;
  width: fit-content;
  margin: auto;
}

@media (max-width: 767px) {
  .contact .box {
    flex-direction: column;
  }
  .contact input {
    flex-basis: 100%;
  }
  .contact input:first-child {
    margin-bottom: 25px;
  }
}
